import React from 'react'
import styles from './Goals.module.scss'


export const Goals = () => {
    return (
        <div className={styles.Goals}>
            <div className={styles.Container}>
                <div className={styles.Message}>
                    <h2>My Goals</h2>
                    <p>Since my childhood i've always had thirst for knowledge, so i've been stadying everything i could.
                        Only now i found my passion in programming, thinking that i lost so much time for things that wasn't that interesting for me, what makes me more focused and makes me spend more time every day stadying more and more.
                        I want to achieve more knowledges and experience in programming and i eager to partake in a lot of hard and iteresting projects with a team of professional programmers
                    </p>
                </div>
            </div>
        </div>
    )
}
